import styled from "styled-components";
const StyledTable = styled.div`
  position: relative;
  width: 100%;
  max-width: 800px;
  /* top: 50%; */
  /* left: 50%; */
  /* transform: translate(-50%, -50%); */

  table {
    width: 100%;
    
    border-collapse: collapse;
    /* overflow: hidden; */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }

  th,
  td {
    padding: 15px;
    background-color: rgba(255, 255, 255, 0.2);
    color: #fff;
    text-align: center;
    align-items: center;
  }

  th {
    text-align: center;
  }

  thead {
    th {
      background-color: #55608f;
    }
  }

  tbody {
    tr {
      &:hover {
        color: white;

        background-color: blanchedalmond;
      }
    }
    td {
      color: blue;
      position: relative;
      &:hover {
        &:before {
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          
          z-index: -1;
        }
      }
    }
      
    }
  
`;
export default StyledTable;
