import { getDatabase, ref, child, get, remove } from "firebase/database";
import { useState } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import StyledButton from "../styles/StyledButton.js";
import StyledTable from "../styles/StyledTable.js";
import { NewContext } from '../App';
import { useContext } from "react";

const Mainpage = ({ data }) => {
  const {newData,setNewData} = useContext(NewContext)
  
  console.log(data);
  function handleDelete(del){
    const db = getDatabase()
    remove(ref(db,`users/${del}`))
    .then(setNewData(!newData))

    
    console.log(`removed data${del}`)
  }

  return (
    <>
      <StyledTable>
        <table>
          <thead>
            <tr>
              <th>First name</th>
              <th>Last name</th>
              <th>Card number</th>
              <th>Phone number</th>
              <th>Actions</th>
            </tr>
          </thead>

          <tbody>
            {data.map((patient) => {
              const id = patient.key;
              console.log(patient.key);

              return (
                <tr key={patient.key}>
                  <td>{patient.firstName}</td>
                  <td>{patient.lastName}</td>
                  <td>{patient.cardNumber}</td>
                  <td>{patient.phone}</td>
                  <td>
                    <Link to={`./edit/${id}`}>
                      <StyledButton>edit</StyledButton>
                    </Link>
                    {/* <Link to={`/view/${patient.key}`}> */}
                      {/* <StyledButton>view</StyledButton> */}
                    {/* </Link> */}
                    <StyledButton onClick={()=>handleDelete(patient.key)}>delete</StyledButton>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </StyledTable>
    </>
  );
};

export default Mainpage;
